import { useState } from "react";
import "./Work.css";

const Work = () => {
  const [projects, setProjects] = useState([
    {
      id: 0,
      url: "https://res.cloudinary.com/dgubzvsvn/image/upload/v1680332666/cv/portfolio-threejs/0_lzl6im.jpg",
    },
    {
      id: 1,
      url: "https://res.cloudinary.com/dgubzvsvn/image/upload/v1680332668/cv/portfolio-threejs/1_cp49kq.jpg",
    },
    {
      id: 2,
      url: "https://res.cloudinary.com/dgubzvsvn/image/upload/v1680332669/cv/portfolio-threejs/2_nylvy4.jpg",
    },
    {
      id: 3,
      url: "https://res.cloudinary.com/dgubzvsvn/image/upload/v1680332669/cv/portfolio-threejs/3_amushz.jpg",
    },
  ]);

  return (
    <section className="work">
      <h2>My projects</h2>
      <div className="projects">
        {projects.map((el, index) => {
          return (
            <div
              className="project"
              key={index}
              style={{
                transform: `translateX(calc(-100% * ${index + 1}))`,
                animationDelay: `calc(0.3s * ${index})`,
              }}
            >
              <span />
              <img src={el.url} alt="img" />
              <div className="box">
                <p>
                  Lorem ipsum dolor sit, amet consectetur adipisicing elit.
                  Distinctio doloribus praesentium enim, nam quidem ad sequi
                  repellat quod illo maxime voluptatum nisi, rem dolorum non,
                  architecto beatae assumenda sit velit?
                </p>
                <button>VIEW</button>
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
};

export default Work;
