import { useEffect, useMemo, useState } from "react";
import { init } from "../threejs";

const Home = () => {
  const [isRendering, setIsRendering] = useState(false);

  useMemo(() => setIsRendering(true), [isRendering]);

  useEffect(() => {
    if (isRendering) {
      init();
    }
  }, [isRendering]);

  return (
    <div className="app">
      <div class="lel" id="container">
        <h1 id="christopherLis">Patryk Szczerbiński</h1>
        <p id="oneWithAn">
          Somewhere in the space we need to have a good portfolio!
        </p>
        <button id="viewWorkBtn">View Work</button>
      </div>
    </div>
  );
};

export default Home;
